.benchmarks {
  background: #ffffff;
  padding: 18px 20px;
  border-bottom: 4px solid #f16546;
}
.benchmarks__percents {
  display: flex;
  justify-content: flex-start;
  padding: 15px 0;
}
.benchmarks__title {
  text-decoration: none;
  border-bottom: 1px solid transparent;
}
.percents__item {
  text-decoration: none;
  display: flex;
  align-items: center;
}
.percents__item:nth-child(2) {
  margin-left: 20px;
}
.percents__count {
  font-size: 28px;
  letter-spacing: 0.01em;
  text-transform: uppercase;
}
.percents__count--green {
  color: #00AA45;
}
.percents__count--red {
  color: #FB373F;
}
.percents__text {
  font-size: 12px;
  line-height: 16px;
  color: #797979;
  width: 140px;
  margin-left: 10px;
}
