.banners__sales-audit-banner {
  position: relative;
  max-width: 271px;
  background: #FFFFFF;
  border-left: 4px solid #fcde63;
  flex-shrink: 0;
  flex: 0.94;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  padding: 24px 66px 32px 21px;
  margin-right: 12px;
}
.banners__sales-audit-banner:after {
  content: "";
  display: block;
  width: 95px;
  height: 98px;
  background: url("./img/graphs-img.svg");
  position: absolute;
  bottom: -4px;
  right: -7px;
  z-index: 0;
}
.banners__big-banner {
  flex-shrink: 0;
  flex: 2;
  padding: 24px 24px 24px 21px;
  transition: all 0.3s;
  background: #ffffff;
  border-left: 4px solid #fcde63;
  position: relative;
}
.banners__big-banner:after {
  content: "";
  display: block;
  position: absolute;
  background: url("./img/img_CA.png") no-repeat;
  bottom: 0;
  right: 0;
  width: 128px;
  height: 111px;
  opacity: 1;
  transition: all 0.3s;
}
